export const sourceTypeValidator = (value) => value !== '';
export const sourceNameValidator = (value) => new RegExp('^.').test(value);
export const ocpClusterIdValidator = (value) => new RegExp('^.').test(value);
export const awsS3BucketNameValidator = (value) => new RegExp('^[A-Za-z0-9]+[A-Za-z0-9_-]*$').test(value);
export const arnValidator = (value) => {
    const arnPrefix = 'arn:aws:';
    if (value.length < arnPrefix.length) {
        return false;
    }
    if (value.length === arnPrefix.length) {
        return value === arnPrefix;
    }
    return value.indexOf(arnPrefix) === 0;
};
